import {AppComponent} from "./app.component";
import {Routes, RouterModule} from "@angular/router";
import {MonitoringComponent} from "./landing-page/monitoring/monitoring.component";
import {InformationComponent} from "./landing-page/information/information.component";
import {LandingPageComponent} from "./landing-page/landing-page.component";

export const APP_ROUTES: Routes = [
  {path: '', redirectTo: '/landingPage', pathMatch: 'full'},
  {path: 'landingPage', component: LandingPageComponent},
  {path: 'monitoring', component: MonitoringComponent},
  {path: 'informationen', component: InformationComponent, }
]


export const routing = RouterModule.forRoot(APP_ROUTES);



