import { Component, OnInit } from '@angular/core';
import {Observable} from "rxjs";
import {Http} from "@angular/http";
import {FormGroup, FormControl} from "@angular/forms";

@Component({
  selector: 'app-information',
  templateUrl: 'information.component.html',
  styleUrls: ['information.component.css']
})
export class InformationComponent implements OnInit {
  sensorDataForm: FormGroup;
  kvArray = [['heartrate', ''],['humidity', ''],['object_temp', ''],['light', ''],['ambient_temp', ''],['air_pressure', '']];
  constructor(private  http: Http) { }

  ngOnInit() {
    this.sensorDataForm = new FormGroup({
      'heartrate': new FormControl(''),
      'humidity': new FormControl(''),
      'object_temp': new FormControl(''),
      'light': new FormControl(''),
      'ambient_temp': new FormControl(''),
      'air_pressure': new FormControl('')
    });
    this.getSensorDate()
  }

  getSensorDate(){
    Observable.interval(2000)
    //http://localhost:8080/test
    //https://w4praesi.mybluemix.net/test
      .switchMap(() => this.http.get('https://w4pfronti.mybluemix.net/sensordata')).map((data) => data)
      .subscribe((data) => {
        this.kvArray['heartrate'] = data.json().heartrate;
        this.kvArray['humidity'] = data.json().humidity;
        this.kvArray['object_temp'] = data.json().object_temp;
        this.kvArray['light'] = data.json().light;
        this.kvArray['ambient_temp'] = data.json().ambient_temp;
        this.kvArray['air_pressure'] = data.json().air_pressure;
        console.log(data.text())
      });
  }

}



